"use client";

import { PieChart, Pie, Cell, Tooltip, Legend, ResponsiveContainer } from "recharts";

const COLORS = [
  "#ef4444", "#f97316", "#eab308", "#84cc16", "#22c55e",
  "#0ea5e9", "#6366f1", "#a855f7", "#ec4899", "#14b8a6"
];


function formatPesoShort(value) {
  if (value >= 1_000_000) return `₱${(value / 1_000_000).toFixed(2)}M`;
  if (value >= 1_000) return `₱${(value / 1_000).toFixed(1)}K`;
  return `₱${value.toLocaleString()}`;
}

const renderLabel = ({ name, value }) => {
  return `${name}: ${formatPesoShort(value)}`;
};

// --- COA-COMPLIANT EXPENSE CATEGORIZER ---
function categorizeExpense(description = "") {
  const t = description.toLowerCase();

  // PERSONAL SERVICES (PS)
  if (
    t.includes("hon.") ||
    t.includes("honor") ||
    t.includes("appointed") ||
    t.includes("elected") ||
    t.includes("tanod") ||
    t.includes("bhw") ||
    t.includes("chief")
  ) {
    return "PS (Personal Services)";
  }

  // SK FUND
  if (t.includes("sk")) return "10% SK Fund";

  // BDRRM FUND
  if (
    t.includes("fire") ||
    t.includes("safety drill") ||
    t.includes("disaster") ||
    t.includes("rescue") ||
    t.includes("drrm")
  ) {
    return "5% BDRRM Fund";
  }

  // GAD PROGRAMS
  if (
    t.includes("women") ||
    t.includes("senior") ||
    t.includes("pwd") ||
    t.includes("nutrition") ||
    t.includes("feeding") ||
    t.includes("deworm") ||
    t.includes("gap")
  ) {
    return "GAD Programs";
  }

  // CAPITAL OUTLAY / 20% BDF
  if (
    t.includes("construction") ||
    t.includes("improvement") ||
    t.includes("materials") ||
    t.includes("solar") ||
    t.includes("equipment") ||
    t.includes("road") ||
    t.includes("building") ||
    t.includes("water system") ||
    t.includes("rehab")
  ) {
    return "20% BDF / Capital Outlay";
  }

  // MOOE
  if (
    t.includes("travel") ||
    t.includes("seminar") ||
    t.includes("training") ||
    t.includes("supplies") ||
    t.includes("office") ||
    t.includes("water") ||
    t.includes("electric") ||
    t.includes("bond") ||
    t.includes("security") ||
    t.includes("bookkeeper") ||
    t.includes("lumber") ||
    t.includes("fuel") ||
    t.includes("diesel") ||
    t.includes("registration") ||
    t.includes("check book") ||
    t.includes("maintenance") ||
    t.includes("interest") ||
    t.includes("tax") ||
    t.includes("pcf")
  ) {
    return "MOOE";
  }

  // DEFAULT
  return "Other / Non-Office";
}

export default function ExpensePieChart({ transactions }) {
  // Only expense rows
  const expenseTx = transactions.filter(t => t.withdrawal > 0);

  const grouped = {};
  expenseTx.forEach(t => {
    const category = categorizeExpense(t.description || ""); // FIXED HERE
    grouped[category] = (grouped[category] || 0) + t.withdrawal;
  });

  const chartData = Object.keys(grouped).map(key => ({
    name: key,
    value: grouped[key],
  }));

  return (
    <div className="w-full h-[350px] mt-10">
      <h2 className="font-semibold text-lg mb-3">Expense Breakdown</h2>

      <ResponsiveContainer>
        <PieChart>
          <Pie
            data={chartData}
            cx="50%"
            cy="50%"
            outerRadius={100}
            dataKey="value"
            labelLine={true}
            label={renderLabel}   // <-- Peso labels outside
          >
            {chartData.map((entry, index) => (
              <Cell key={index} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>


          <Tooltip />
          <Legend />
        </PieChart>
      </ResponsiveContainer>
    </div>
  );
}
