"use client";

import { useState, useEffect } from "react";
import { Card } from "@/components/ui/card";
import CashflowChart from "./components/cashflowChart.jsx";
import TransactionTable from "./components/transactionTable";
import ToggleButton from "./components/toggleButton";
import { useCashflowData } from "./components/useCashflowData";
import { supabase } from "../../../lib/supabaseClient";
import KPICards from "./components/kpiCards";
import IncomePieChart from "./components/incomePieChart";
import ExpensePieChart from "./components/expensePieChart";

export default function Page() {
  const { transactions: fetchedTransactions, chartData, loading, refetch } = useCashflowData();
  const [transactions, setTransactions] = useState([]);
  const [showTable, setShowTable] = useState(false);

  // Sync fetched transactions
  useEffect(() => {
    setTransactions(fetchedTransactions || []);
  }, [fetchedTransactions]);

  // -----------------------------
  // ADD NEW TRANSACTION
  // -----------------------------
  const handleAddTransaction = async (newTransaction) => {
    try {
      const lastBalance = transactions.length
        ? transactions[transactions.length - 1].balance
        : 0;

      const newBalance =
        lastBalance + (newTransaction.deposit || 0) - (newTransaction.withdrawal || 0);

      const transactionToInsert = { ...newTransaction, balance: newBalance };

      const { data, error } = await supabase
        .from("transactions")
        .insert([transactionToInsert])
        .select();

      if (error) throw error;

      setTransactions((prev) => [...prev, data[0]]);
      refetch();
    } catch (err) {
      console.error("Failed to add transaction:", err.message);
      alert("Error adding transaction.");
    }
  };

  // -----------------------------
  // DELETE
  // -----------------------------
  const handleDeleteTransaction = async (id) => {
    if (!id) return;

    const confirmDelete = window.confirm("Are you sure you want to delete this transaction?");
    if (!confirmDelete) return;

    try {
      await supabase.from("transactions").delete().eq("id", id);

      const updated = transactions.filter((t) => t.id !== id);

      let runningBalance = 0;
      const recalculated = updated.map((t) => {
        runningBalance += (t.deposit || 0) - (t.withdrawal || 0);
        return { ...t, balance: runningBalance };
      });

      setTransactions(recalculated);
      refetch();
    } catch (err) {
      console.error("Failed to delete transaction:", err.message);
      alert("Error deleting transaction.");
    }
  };

  // -----------------------------
  // KPI Calculations
  // -----------------------------
  const totalIncome = chartData?.reduce((a, r) => a + (r.income || 0), 0) || 0;
  const totalExpense = chartData?.reduce((a, r) => a + (r.expense || 0), 0) || 0;

  const beginningBalance = transactions.length ? transactions[0].balance : 0;
  const lastBalance = transactions.length
    ? transactions[transactions.length - 1].balance
    : 0;

  const lastMonth = chartData.length ? chartData[chartData.length - 1] : null;
  const monthlySurplus = lastMonth
    ? (lastMonth.income || 0) - (lastMonth.expense || 0)
    : 0;

  const kpiTotals = {
    beginningBalance,
    totalIncome,
    totalExpense,
    runningBalance: lastBalance,
    monthlySurplus,
  };

  // -----------------------------
  // EDIT TRANSACTION
  // -----------------------------
  const handleEditTransaction = async (updatedTransaction, id) => {
    try {
      const index = transactions.findIndex((t) => t.id === id);
      if (index === -1) throw new Error("Transaction not found");

      const previousBalance = index > 0 ? transactions[index - 1].balance : 0;
      const newBalance =
        previousBalance +
        (updatedTransaction.deposit || 0) -
        (updatedTransaction.withdrawal || 0);

      const transactionToUpdate = { ...updatedTransaction, balance: newBalance };

      const { error } = await supabase
        .from("transactions")
        .update(transactionToUpdate)
        .eq("id", id);

      if (error) throw error;

      const updated = [...transactions];
      updated[index] = transactionToUpdate;

      let runningBalance = newBalance;
      for (let i = index + 1; i < updated.length; i++) {
        const t = updated[i];
        runningBalance += (t.deposit || 0) - (t.withdrawal || 0);
        updated[i] = { ...t, balance: runningBalance };

        await supabase.from("transactions").update({ balance: runningBalance }).eq("id", t.id);
      }

      setTransactions(updated);
      refetch();
    } catch (err) {
      console.error("Failed to edit:", err.message);
      alert("Error editing transaction.");
    }
  };

  // -----------------------------
  // UI LAYOUT
  // -----------------------------
  return (
    <div className="p-6 space-y-6">
      <h1 className="text-2xl font-bold">2025 Cashflow</h1>

      {/* KPI Header */}
      <KPICards totals={kpiTotals} />

      {/* Monthly Cashflow Chart */}
      <Card className="p-4">
        <CashflowChart data={chartData} />
      </Card>

      {/* Income + Expense Side-by-Side */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mt-6">
        <IncomePieChart transactions={transactions} />
        <ExpensePieChart transactions={transactions} />
      </div>

      {/* Transaction Table */}
      <div>
        <ToggleButton showTable={showTable} setShowTable={setShowTable} />
        {showTable && (
          <TransactionTable
            transactions={transactions}
            loading={loading}
            setTransactions={setTransactions}
            onAddTransaction={handleAddTransaction}
            onDeleteTransaction={handleDeleteTransaction}
            onEditTransaction={handleEditTransaction}
          />
        )}
      </div>
    </div>
  );
}
