"use client";

import { PieChart, Pie, Cell, Tooltip, Legend, ResponsiveContainer } from "recharts";

const COLORS = [
  "#22c55e", "#0ea5e9", "#6366f1", "#14b8a6",
  "#84cc16", "#eab308", "#f97316", "#ef4444"
];

function formatPesoShort(value) {
  if (value >= 1_000_000) return `₱${(value / 1_000_000).toFixed(2)}M`;
  if (value >= 1_000) return `₱${(value / 1_000).toFixed(1)}K`;
  return `₱${value.toLocaleString()}`;
}

const renderLabel = ({ name, value }) => {
  return `${name}: ${formatPesoShort(value)}`;
};

// --- AUTO CATEGORIZER FOR INCOME ---
function categorizeIncome(description = "") {
  const t = description.toLowerCase();

  // -------------------------
  // INTERNAL SOURCES
  // -------------------------

  // 1. Clearance & Certification Fees
  if (
    t.includes("clearance") ||
    t.includes("certification")
  ) {
    return "Clearance & Certification fees";
  }

  // 2. Other Business Fees/Income
  if (
    t.includes("business") ||
    t.includes("fees/income") ||
    t.includes("other income")
  ) {
    return "Other business fees/income";
  }

  // 3. Rental Fees
  if (t.includes("rental")) {
    return "Rental fees";
  }

  // 4. Interest Income
  if (t.includes("interest")) {
    return "Interest income";
  }

  // -------------------------
  // EXTERNAL SOURCES
  // -------------------------

  // 5. Real Property Tax
  if (
    t.includes("real property tax") ||
    t.includes("rpt")
  ) {
    return "Real Property Tax";
  }

  // 6. Community Tax
  if (t.includes("community tax")) {
    return "Community Tax";
  }

  // 7. Subsidy from other LGUs
  if (t.includes("subsidy")) {
    return "Subsidy from other LGUs";
  }

  // 8. Internal Revenue Allotment (IRA)
  if (
    t.includes("ira") ||
    t.includes("nta released") ||
    t.includes("internal revenue allotment")
  ) {
    return "Internal Revenue allotment (IRA)";
  }

  // Default if no match
  return "Other Income";
}



export default function IncomePieChart({ transactions }) {
  // Only deposits
  const incomeTx = transactions.filter(t => t.deposit > 0);

  // Group by category
  const grouped = {};
  incomeTx.forEach(t => {
    const category = categorizeIncome(t.description || ""); // FIXED HERE
    grouped[category] = (grouped[category] || 0) + t.deposit;
  });

  const chartData = Object.keys(grouped).map(key => ({
    name: key,
    value: grouped[key],
  }));

  return (
    <div className="w-full h-[350px] mt-10">
      <h2 className="font-semibold text-lg mb-3">Income Breakdown</h2>

      <ResponsiveContainer>
        <PieChart>
          <Pie
            data={chartData}
            cx="50%"
            cy="50%"
            outerRadius={100}
            dataKey="value"
            labelLine={true}
            label={renderLabel}   // <-- Peso labels outside
          >
            {chartData.map((entry, index) => (
              <Cell key={index} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>

          <Tooltip />
          <Legend />
        </PieChart>
      </ResponsiveContainer>
    </div>
  );
}
